* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.timer {
  display: flex;
}
.value,
.label {
  display: block;
  text-align: center;
}
.value {
  font-size: 36px;
}
.label {
  font-size: 12px;
}
.field {
  margin-right: 18px;
}
.btns {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 100px;
  left: 50%;
  transform: translateX(-50%);
  /* margin-right: -50%; */
}
.btn {
  height: 50px;
  width: 80px;
}
